import React from 'react';
import Img from 'gatsby-image';
import { useStaticQuery, graphql } from 'gatsby';
import PropTypes from 'prop-types';

import { useTheme } from '@material-ui/core/styles';
import { Grid, Typography, useMediaQuery } from '@material-ui/core';
import { SectionHeader } from 'components/molecules';

const query = graphql`
  {
    contentfulAsset(file: { fileName: { eq: "RD2.jpg" } }) {
      fluid(maxWidth: 600) {
        ...GatsbyContentfulFluid
      }
    }
  }
`;
const ModifyHouse = props => {
  const data = useStaticQuery(query);
  const { className, ...rest } = props;

  return (
    <div className={className} {...rest}>
      <Grid
        container
        /* direction={isMd ? 'row' : 'column-reverse'} */ spacing={4}
      >
        <Grid item xs={12} md={6}>
          <SectionHeader
            label="RODINN DM"
            title={
              <span>
                Pokud u Vs nebav hledat nekonen dlouhou dobu parkovac
                msto, mte u dost svch soused a v chodb zakopvte o kola a
                lye,{' '}
                <Typography color="primary" variant="inherit" component="span">
                  je tohle msto pesn pro Vs.
                </Typography>
              </span>
            }
            subtitle="Nov modern domky Vm poskytnou naprost soukrom, vlastn msto na parkovn, zahradu kam nikdo nedohldne, vlastn gar, dochzkovou vzdlenost do centra Pbrami a pitom louku a les za domem. 
            "
            align="left"
            fadeUp
            disableGutter
          />
        </Grid>
        <Grid
          item
          xs={12}
          md={6}
          data-aos="fade-up"
          container
          justify="center"
          alignItems="flex-start"
        >
          <Img
            fluid={data.contentfulAsset.fluid}
            alt="obrazek"
            style={{ height: '100%', width: '100%', borderRadius: '10px' }}
          />
        </Grid>
      </Grid>
    </div>
  );
};

ModifyHouse.propTypes = {
  /**
   * External classes
   */
  className: PropTypes.string,
};

export default ModifyHouse;
